body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.form-control label,
.form-control input,
.form-control textarea {
  width: 100%;
  display: block;
}

.form-control label {
  margin-bottom: 0.5em;
} 

.form-control textarea {
  max-width: 100%;
  min-width: 100%;
  min-height: 1.25em;
}

.form-control {
  margin-bottom: 1em;
}

.form-actions button,
.btn {
  background-color: #01d1d1;
  font: inherit;
  border: 1px solid #01d1d1;
  border-radius: 3px;
  padding: 0.5em 1em;
  margin-right: 1em;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.25);
  color: white;
  cursor: pointer;
}

.form-actions button:hover,
.form-actions button:active,
.btn:hover,
.btn:active {
  background-color: #01a7a7;
  border-color: #01a7a7;
}
.auth-form {
  width: 20em;
  max-width: 80%;
  margin: 5em auto;
}

.bookings__list {
  list-style: none;
  margin: 0 auto;
  padding: 0;
  width: 40em;
  max-width: 90%;
}

.bookings__item {
  margin: 0.5em 0;
  padding: 0.5em;
  border: 1px solid #01d1d1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.bookinkgs__item-actions {

}
.bookings-control {
  text-align: center;
  padding: 0.5em;
}

.bookings-control button {
  font: inherit;
  border: none;
  background-color: transparent;
  color: black;
  padding: 0.25em 3em;
  border-bottom: 2px solid transparent;
  outline: none;
  cursor: pointer;
}

.bookings-control button.active {
  border-bottom-color: #01d1d1;
  color: #01d1d1; 
}
.plop-loading {
  display: flex;
  justify-content: center;
  align-items: center;
}

.plop-spinner {
  color: official;
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}
.plop-spinner div {
  -webkit-transform-origin: 32px 32px;
          transform-origin: 32px 32px;
  -webkit-animation: plop-spinner 1.2s linear infinite;
          animation: plop-spinner 1.2s linear infinite;
}
.plop-spinner div:after {
  content: " ";
  display: block;
  position: absolute;
  top: 3px;
  left: 29px;
  width: 5px;
  height: 14px;
  border-radius: 20%;
  background: #01d1d1;
}
.plop-spinner div:nth-child(1) {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-animation-delay: -1.1s;
          animation-delay: -1.1s;
}
.plop-spinner div:nth-child(2) {
  -webkit-transform: rotate(30deg);
          transform: rotate(30deg);
  -webkit-animation-delay: -1s;
          animation-delay: -1s;
}
.plop-spinner div:nth-child(3) {
  -webkit-transform: rotate(60deg);
          transform: rotate(60deg);
  -webkit-animation-delay: -0.9s;
          animation-delay: -0.9s;
}
.plop-spinner div:nth-child(4) {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  -webkit-animation-delay: -0.8s;
          animation-delay: -0.8s;
}
.plop-spinner div:nth-child(5) {
  -webkit-transform: rotate(120deg);
          transform: rotate(120deg);
  -webkit-animation-delay: -0.7s;
          animation-delay: -0.7s;
}
.plop-spinner div:nth-child(6) {
  -webkit-transform: rotate(150deg);
          transform: rotate(150deg);
  -webkit-animation-delay: -0.6s;
          animation-delay: -0.6s;
}
.plop-spinner div:nth-child(7) {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  -webkit-animation-delay: -0.5s;
          animation-delay: -0.5s;
}
.plop-spinner div:nth-child(8) {
  -webkit-transform: rotate(210deg);
          transform: rotate(210deg);
  -webkit-animation-delay: -0.4s;
          animation-delay: -0.4s;
}
.plop-spinner div:nth-child(9) {
  -webkit-transform: rotate(240deg);
          transform: rotate(240deg);
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
}
.plop-spinner div:nth-child(10) {
  -webkit-transform: rotate(270deg);
          transform: rotate(270deg);
  -webkit-animation-delay: -0.2s;
          animation-delay: -0.2s;
}
.plop-spinner div:nth-child(11) {
  -webkit-transform: rotate(300deg);
          transform: rotate(300deg);
  -webkit-animation-delay: -0.1s;
          animation-delay: -0.1s;
}
.plop-spinner div:nth-child(12) {
  -webkit-transform: rotate(330deg);
          transform: rotate(330deg);
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}
@-webkit-keyframes plop-spinner {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes plop-spinner {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.modal {
  position: fixed;
  top: 20vh;
  left: 5%;
  /* width: 50em; */
  width: 90%;
  background-color: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  z-index: 100;
}

.modal__header {
  padding: 1em;
  background-color: #01d1d1;
  color: white;
}

.modal__header h1 {
  margin: 0;
  font-size: 1.25em;
}

.modal__content {
  padding: 1em;
}

.modal__actions {
  display: flex;
  justify-content: flex-end;
  padding: 1em;
}

@media (min-width: 768px) {
  .modal {
    width: 30em;
    left: calc((100% - 30em) / 2);
  }
}
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.75);
}
.events__list-item {
  display: flex;
  justify-content: space-between;
  margin: 1em 0;
  padding: 1em;
  border: 1px solid #01d1d1;
  align-items: center;
}

.events__list-item h1 {
  margin: 0;
  font-size: 1.5em;
  color: #01d1d1;
}

.events__list-item h2 {
  margin: 0;
  font-size: 1em;
  color: lightslategrey;
}

.events__list-item p {
  margin: 0;
}
.event__list {
  width: 40em;
  max-width: 90%;
  margin: 2em auto;
  list-style: none;
  padding: 0;
}
.events-control {
  text-align: center;
  border: 1px solid #01d1d1;
  padding: 2em;
  margin: 2em auto;
  width: 30em;
  max-width: 80%;
}
.main-navigation {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 3.5em;
  background-color: #01d1d1;
  padding: 0 1em;
  display: flex;
  align-items: center;
}

.main-navigation__logo h1 {
  margin: 0;
  font-size: 1.5em;
}

.main-navigation__items {
  margin-right: 1.5em;
  width: 100%;
}

.main-navigation__items ul {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  list-style: none;
  padding: 0;
  margin: 0;
  align-items: center;
}

.main-navigation__items li {
  margin: 0 1em;
}

.main-navigation__items a,
.main-navigation__items button {
  text-decoration: none;
  color: white;
  padding: 0.25em 0.5em;
  border: none;
  font: inherit;
  background-color: transparent;
  cursor: pointer;
  vertical-align: middle;
  margin: 0;
}

.main-navigation__items a:hover,
.main-navigation__items a:active,
.main-navigation__items a.active,
.main-navigation__items button:hover,
.main-navigation__items button:active {
  background-color: white;
  color: blue;
  border-radius: 5px;
}
.main-content {
  margin: 4em 2.5em;
}
